.AddData{
  margin: 1px;
}

.textbox-label{
    font-size: 14px;
}

.AddDataTextBox{
    outline: none;
    background: none;
    padding: 8px 20px;
    
    border: 1px solid #9e9e9e;
    border-radius: 16px;
    color: rgb(255, 36, 102);
}

.AddDataTextBox:focus{
    border: 1px solid rgb(255, 36, 102);;
}

.validation-message{
    display: block;
    font-size: 11px;
    color: red;
}

.validation-message-hide{
    display: none;
}

.submit-btn{
    outline: none;
    background: none;
    /* border: 1px solid #9e9e9e; */
    color: inherit;
    padding: 8px 16px 8px 16px;
}

.submit-btn:hover{
    border: 1px solid rgb(129, 209, 255);
    color: rgb(129, 209, 255);
}

.msg{
    padding: 12px;
}

.AddItemModal{
    position: fixed; 
    left: 0;
    top: 0;
    
    width: 100%;
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0,0,0); 
    background-color: rgba(0, 0, 0, 0.8); 

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    animation: AddItemModalAnimation 0.3s;
}

@keyframes AddItemModalAnimation {
     0%{
        opacity: 0;
        -webkit-transform:translate(0,-20px); 
        
    }
    100%{
        opacity: 60;
        -webkit-transform:translate(0,-0);
    }
}

.AddItemModal-hide{
    display: none;
}

.AddBuySell{
    padding-top: 50px;
}

.weAreAdding{
    padding: 12px 50px 12px 50px;
    border-radius: 20px;
}

.weAreToppingUp{
    border: 2px solid rgba(255, 252, 79, 0.6);
}

.weAreWithdrawing{
    border: 2px solid rgba(79, 249, 255, 0.6);
}

.weAreFXing{
    border: 2px solid rgba(255, 195, 64, 0.6);
}

.weAreBuying{
    border: 2px solid rgba(0, 255, 0, 0.60);
}

.weAreSelling{
    border: 2px solid rgba(255, 0, 0, 0.600);
}